
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { getMonth, getYear, getDate, addYears } from 'date-fns';
import { ko } from "date-fns/locale";

function Adoptt() {
  const [data, setData] = useState([]); //data
  const [upkind, setUpkind] = useState(''); //     
  const [upstate, setState] = useState(''); //     

  useEffect(() => {
    if (upkind !== '') {
      const apiUrl = 'http://apis.data.go.kr/1543061/abandonmentPublicSrvc/abandonmentPublic'

      const params = {
        pageNo: 1,
        numOfRows: 5,
        orgdownNm: '',
        upkind: upkind,
        org_cd: '',
        state: '',
        serviceKey: 'TZfsVHVWd9fA3Rqbp2a6xaGgeoqpLx0eWKAWs5Cfh2oNP38l5bl5c0yfwr1cMcJRbDxDgC7EbSI8GaCgLoZMaQ==',
      };

      axios.get(apiUrl, { params })
        .then(response => {
          const convert = require('xml-js');
          // XML  JSON 
          const jsonData = convert.xml2json(response.data, { compact: true, spaces: 4 });
          // jsonData JavaScript  
          const parsedData = JSON.parse(jsonData);

          //    
          setData(parsedData.response.body.items.item);
        })
        .catch(error => {
          console.error('Error fetching data:', error);
        });
    }
  }, [upkind]);

  const handleUpkindChange = (event) => {
    const selectedUpkind = event.target.value;
    setUpkind(selectedUpkind);
  };

  

  return (
    <article className='adoptpage'>
      <div className='search'>
        <div className='select'>
          <div className='select2'>
            <select>
              <option value=""></option>
              <option value="notice"></option>
              <option value="protect"></option>
            </select>
            <select>
              <option>, </option>
              <option value="6110000"></option>
              <option value="6260000"></option>
              <option value="6270000"></option>
              <option value="6280000"></option>
              <option value="6290000"></option>
            </select>
            <select onChange={handleUpkindChange}>
              <option value=""> </option>
              <option value="417000"></option>
              <option value="422400"></option>
              <option value="429900"></option>
            </select>
          </div>
        </div>
      </div>
      <div className='adoptlist'>
      {data.map((item, index) => (
        <div className='adopt' key={index}>
          <div><img src={item.popfile._text}/></div>
          <div>
            <h2><span></span>{item.desertionNo._text}</h2>
            <p><span>: </span>{item.kindCd._text}</p>
            <p><span> : </span>{item.age._text}</p>
            <p><span> : </span>{item.careNm._text}</p>
            <p><span> : </span>{item.processState._text}</p>
          </div>
        </div>
        ))}
      </div>
      <div className='pagenum'>
        <button>&lt;</button>
        <button>1</button>
        <button>2</button>
        <button>3</button>
        <button>4</button>
        <button>5</button>
        <button>&gt;</button>
      </div>
    </article>
  )
}

//https://yelee.tistory.com/90
//https://yohanpro.com/posts/react/react-datepicker

export default Adoptt